import React from "react";
import styled from "styled-components";
import { useAddStudent } from "../../../Context/AddStudent";

const OpenExcelFile = () => {
  const students = useAddStudent();
  return (
    <ExcelFileReader>
      <TablePanel>
        <table style={{ width: "100%" }}>
          <tbody>
            {students.rows.map((data, idx) => {
              if (idx === 0) {
                return (
                  <tr key={data[0]}>
                    <TH>{data[0]}</TH>
                    <TH>{data[1]}</TH>
                  </tr>
                );
              } else
                return (
                  <TR key={data[0]}>
                    <TD>{data[0]}</TD>
                    <TD>{data[1]}</TD>
                  </TR>
                );
            })}
          </tbody>
        </table>
      </TablePanel>
      <div>
        <div>
          <button
            style={{
              background: "#be2edd",
              color: "#fff",
              fontWeight: 600,
              marginBottom: "10px",
              borderRadius: "3px",
            }}
            onClick={() => {
              students.handleResetRowsAndArray();
              document.getElementsByName("oFile").values = null;
            }}
          >
            Remove
          </button>
        </div>
        <div style={{ display: "flex" }}>
          <p
            style={{
              fontSize: "1rem",
              marginBottom: "0",
              fontWeight: "600",
            }}
          >
            File Name:{" "}
          </p>
          <p style={{ fontSize: "1rem", marginBottom: "0", marginLeft: "5px" }}>
            {students.xlFile.name}
          </p>
        </div>
        {/* <div style={{ display: "flex" }}>
          <p
            style={{
              fontSize: "1rem",
              marginBottom: "0",
              fontWeight: "600",
            }}
          >
            Last Modified:{" "}
          </p>
          <p style={{ fontSize: "1rem", marginBottom: "0", marginLeft: "5px" }}>
            {xlFile.lastModifiedDate.toString().substring(0, 33)}
          </p>
        </div> */}
        <div style={{ display: "flex" }}>
          <p
            style={{
              fontSize: "1rem",
              marginBottom: "0",
              fontWeight: "600",
            }}
          >
            Size:{" "}
          </p>
          <p style={{ fontSize: "1rem", marginBottom: "0", marginLeft: "5px" }}>
            {students.xlFile.size} Kb
          </p>
        </div>
      </div>
    </ExcelFileReader>
  );
};

export default OpenExcelFile;

const ExcelFileReader = styled.div`
  margin: 0.2rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 0;
  background: rgba(240, 240, 240, 0.5);
  padding: 0.3rem;
  border-radius: 5px;
  border: 1px solid rgba(3, 3, 3, 0.1);
`;

const TD = styled.td`
  border: 1px solid rgba(3, 3, 3, 0.5);
  min-weight: 150px;
  padding: 0.3rem 0.5rem;
`;
const TH = styled.th`
  border: 1px solid rgba(3, 3, 3, 0.5);
  min-weight: 150px;
  padding: 0.3rem 0.5rem;
  font-weight: 600;
  background: green;
  color: #fff;
`;

const TR = styled.tr`
  &:nth-child(2n + 1) {
    background: rgba(200, 230, 230, 0.3);
  }
`;

const TablePanel = styled.div`
  height: 400px;
  overflow: hidden scroll;
  margin-bottom: 1rem;
  border: 1px solid rgba(3, 3, 3, 0.5);
  &::-webkit-scrollbar {
    width: 5px;
  }
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: rgba(3, 3, 3, 0.25);
    border-radius: 50px;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: rgba(3, 3, 3, 0.4);
  }
  @media (max-width: 410px) {
    width: 100%;
  }
`;
